import './card.css'
function videos(){
    const vid  =  document.createElement('div');
    vid.classList.add('videos');

    return vid
}
function videocard(){
    const movie = document.createElement('div');
    movie.classList.add('videocard')

    return movie
}

function videosbase(){
    const vidb  =  document.createElement('div');
    vidb.classList.add('videosbase');
    let vc = videocard();
    let vd = videos();
    vidb.appendChild(vc)
    vidb.appendChild(vd)
    const watchnowbutton = document.createElement('a');
    watchnowbutton.classList.add('watchbutton')
    watchnowbutton.setAttribute('href','wathnow');
    watchnowbutton.innerHTML = "WATCH NOW"
    watchnowbutton.addEventListener('mouseover',e=>{
        vc.classList.add('active')
        vd.classList.add('active')
    })
    watchnowbutton.addEventListener('mouseout',e=>{
        vc.classList.remove('active')
        vd.classList.remove('active')
    })
    vidb.appendChild(watchnowbutton)
    return vidb
}


function card(){
    const cards = document.createElement('div');
    cards.classList.add('cards');
    let vb = videosbase();
    vb.firstChild.setAttribute('id', 'v1');
    let vb2 = videosbase();
    vb2.firstChild.setAttribute('id', 'v2');

    cards.appendChild(vb)
    cards.appendChild(vb2)

    return cards
}

export {card}